header {
  width: 400px;
  border-radius: 20px;
  background-color: azure;
  box-sizing: border-box;
  padding: 20px;
  color: black;
  box-shadow: 5px 5px 10px rgba(203, 189, 189, 0.7);
  margin: 10px auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
@media screen and (max-width: 450px) {
  header {
    width: 300px;
  }
}
